<template>
<div id="leave">
    <el-table :data="data" border style="width:70.1%" v-loading="load">
        <el-table-column prop="userNo" label="用户" width="180"></el-table-column>     
        <el-table-column prop="staffName" label="工作人员" width="180"></el-table-column>       
        <el-table-column prop="messageType" label="留言类型" width="220"></el-table-column>       
        <el-table-column prop="messageContent" label="内容" width="200"></el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
                <el-button type="text" @click="deleteWindows(scope.row)" size="small">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
</div>
</template>
<script>
export default {
    data(){
        return{
            data:[],
            load:true
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        init(){
            this.axios.getleaveMes({}).then((res)=>{
                if(res.success){
                    this.load=false
                    this.data = res.dataList
                }else{
                    this.$message.error('查询留言信息失败,请检查网络设置')
                }
            })
        },
        deleteWindows(row){
            this.axios.removeLeaveMes({'id':row.id}).then((res)=>{
                if(res.success){
                    this.$message.success(res.message)
                    this.init()
                    return
                }
                this.$message.error('删除失败，请检查网络设置')
            })
        }
    }
}
</script>
<style lang="scss">
#leave{
    .add{
        position: relative;
        top: 100px;
        left: 365px;
    }
    .el-table{
        border:1px solid #000;
        border-radius: 7px 7px 7px 7px;
        box-shadow:0px 0px 3px #000;
        position: relative;
        top:100px;
        left: 150px;
    }
}
</style>

